* {
  box-sizing: border-box;
}

@media (min-width: 100px) {
  .Footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    width: 100%;
    height: 100%;
    background-color: pink;
    padding: 1rem;

    .Adress,
    .Hours,
    .NewsLetter,
    .Logo {
      text-align: center;
      line-height: 25px;

      p {
        font-size: 12px;
      }

      h3 {
        margin-bottom: 0.5rem;
        font-size: 20px;
        font-weight: bold;
        text-transform: uppercase;
      }
    }

    .Adress {
      .icons {
        display: flex;
        gap: 1rem;
        justify-content: center;
        margin-top: 1rem;

        .icon_facebook {
          background-image: url(/image/icon_facebook.svg);
          background-repeat: no-repeat;
          width: 30px;
          height: 30px;
        }
        .icon_instagram {
          background-image: url(/image/icon_instagram.svg);
          background-repeat: no-repeat;
          width: 30px;
          height: 30px;
        }
      }
    }

    .Logo {
      background-image: url(/image/Logo1.png);
      background-repeat: no-repeat;
      width: 252.05px;
      height: 135.37px;
      order: 5;
      margin-left: 1.6rem;
    }

    .NewsLetter {
      display: flex;
      flex-direction: column;
      align-items: center;

      ::placeholder {
        color: grey;
        // padding-left: 23px;
        font-size: 20px;
        text-align: center;
      }

      input {
        width: 236px;
        height: 56px;
        border-radius: 0.5rem;
        border: 1px solid grey;
        background: none;
        font-size: 24px;
        font-weight: bold;
        margin-top: 0.5rem;
      }

      button {
        font-size: 12px;
        height: 44px;
        width: 121.62px;
        border-radius: 0.5rem;
        text-transform: uppercase;
        letter-spacing: 0.7px;
        padding: 1em 0.5em;
        margin-top: 1rem;
      }
    }
  }
}

@media (min-width: 760px) {
  .Footer {
    flex-direction: row;
    justify-content: space-around;
    gap: 0.8rem;
    align-items: unset;

    .Logo {
      order: unset;
      background-image: url(/image/Logo1.png);
      background-repeat: no-repeat;
      width: 194.52px;
      height: 102.264px;
      margin: 0;
      align-self: center;
    }
  }
}

@media (min-width: 1200px) {
  .Footer {
    .Logo {
      background-image: url(/image/Logo1.png);
      background-repeat: no-repeat;
      width: 252.05px;
      height: 135.37px;
      margin-left: 1.6rem;
    }

    .NewsLetter {
      text-align: left;
      display: block;

      ::placeholder {
        text-align: left;
        padding-left: 23px;
        font-size: 20px;
      }

      input {
        width: 336px;
        height: 56px;
        margin: 1rem 1rem 0 -0.3rem;
        padding: 0;
      }

      button {
        align-self: stretch;
      }
    }
  }
}
